<template>
  <div>
    <!-- Header -->
    <header class="w3-row">
      <div class="w3-col m2" id="info-time">
        <ingame-timer id="timer" :time="count" />
        <p id="day">day: {{ progress.nowDay }}</p>
      </div>
      <div class="w3-col m7 border border-secondary px-3" id="info">
        <p v-show="progress.nowDay === 0 && myInfo.job === ''">
            .
        </p>
        <p v-show="progress.nowDay === 0 && myInfo.job !== ''">
             .
        </p>
        <p v-show="progress.nowDay !== 0 && progress.isDay">
           .
        </p>
        <p v-show="progress.isVoteDay">  .</p>
        <p v-show="progress.isVoteDayResult">
          {{ progress.nowDay }}    .
        </p>
        <p v-show="progress.isNight"> .</p>
        <p v-show="progress.isNightResult">
          {{ progress.nowDay }}   .
        </p>
      </div>
      <div class="w3-col m1"></div>
    </header>
  </div>
</template>

<script>
import IngameTimer from "./IngameTimer.vue";

export default {
  data() {
    return {
      ingameNow: "",
    };
  },
  props: {
    count: Object,
    progress: Object,
    myInfo: Object,
  },
  components: {
    IngameTimer,
  },
};
</script>

<style>
#info-time {
  text-align: center;
  height: 115px;
}

#timer {
  font-size: 30px;
}

#day {
  font-size: 20px;
}

#info {
  height: 115px;
  font-size: 35px;
}
</style>
