<script>
//    uri -> ("/sendMafia/{roomNo}")
//      uri -> ("/receiveMafia")

// : 1. isSkiped     ->   isSkiped true  ->     

export default {
  data() {
    return {
      isjobRollCenter: true,
      isJobRollOpen: false,
      job: 'citizen',
    }
  },
  methods: {
    switchJobRoll() {
      this.isjobRollCenter = false
      this.isJobRollOpen = false
    },
    openJobRoll () {
      if (this.isjobRollCenter) {
        this.isJobRollOpen = true
      } else {
      setTimeout(() => {
        this.isJobRollOpen = true
      }, 700)
      if (!this.isjobRollCenter) {
        this.isjobRollCenter = true
      }      
      }
    }
  },
  computed: {
  },
  watch() { //      ?
  },
}
</script>

