<template>
  <div :class="{'ingameNight': progress.isNight, 'ingameDay': progress.isDay, 'ingameDayVote': progress.isVoteDay, 'ingameDayVoteResult': progress.isVoteDayResult, 'ingameNightResult': progress.isNightResult}">
    <div>  </div>
    <br>
    <br>
    <br>
    <button @click="transResultPage">!!</button>

    <div :class="{'voteResultDay': progress.isVoteDayResult, 'voteResultNight': progress.isNightResult}">
      <!-- <h1 v-if="!!deadColor && progress.isVoteDayResult" style="text-align: center; color: black;"> <br>{{ whoIsGone }}<br>!</h1> -->
      <h1 v-if="!!deadColor && progress.isVoteDayResult" style="text-align: center; color: black;">{{ showingMsg }}</h1>
      <h1 v-if="!!deadColor && progress.isNightResult" style="text-align: center; color: white;"> <br>{{ whoIsGone }}<br>,,,</h1>
      <h1 v-if="!deadColor">   </h1>
      <div @click="getOut" :class="{
        'redChar': deadColor === 'red',
        'redCharGone': deadColor === 'red' && isGone,
        'blueChar': deadColor === 'blue',
        'blueCharGone': deadColor === 'blue' && isGone,
        'whiteChar': deadColor === 'white',
        'whiteCharGone': deadColor === 'white' && isGone,
        'blackChar': deadColor === 'black',
        'blackCharGone': deadColor === 'black' && isGone,
        'yellowChar': deadColor === 'yellow',
        'yellowCharGone': deadColor === 'yellow' && isGone
        }"></div>
        <div v-if="!!deadColor" :class="{
          'deadShadow': true,
          'deadShadowGone': isGone,
          }"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      progress: {
        isVoteDayResult: true,
        isNightResult: false,
      },
      deadColor: "", //     ?    id gameInfos           .
      // ?  ""  , ""    .     
      isGone: false, //        false   
      whoIsGone: "", //          .     
      msgCnt: 0, //     
      showingMsg: "", //    
      stopCnt: 0, //      setInterval   
      stopCnt2: 0, //      setInterval   
    }
  },
  created() {
    this.deadColor = 'red';
    this.stopCnt = setInterval(this.typeEffect, 150); //             
    // this.stopCnt2 = setInterval(this.typeEffect2, 150); //             
  },
  methods: {
    typeEffect2() {
      this.msgCnt += 1;
      this.showingMsg = `  ${ this.whoIsGone }  ,,,`.slice(0, this.msgCnt);
      if (this.msgCnt === 50) {
        clearInterval(this.stopCnt2);
        this.stopCnt2 = 0;
      }
    },
    typeEffect() {
      this.msgCnt += 1;
      this.showingMsg = `  ${ this.whoIsGone }  !`.slice(0, this.msgCnt);
      console.log("hi")
      if (this.msgCnt === 50) {
        clearInterval(this.stopCnt);
        this.stopCnt = 0;
      }
    },
    getOut() {
      if (this.deadColor === "red") {
        this.isGone = true;
      }
    },
    transResultPage() {
      this.progress.isVoteDayResult = !this.progress.isVoteDayResult;
      this.progress.isNightResult = !this.progress.isNightResult;
      this.isGone = false;
      this.msgCnt = 0;
    },
    redToBlue() {
      this.deadColor = 'blue';
      setTimeout(() => {
        this.blueToRed();
      }, 10000);
    },
    blueToRed() {
      this.deadColor = 'red';
      setTimeout(() => {
        this.redToBlue();
      }, 10000);
    },
  },
}
</script>

<style>
.voteResultDay {
  z-index: 1800;
  box-shadow : rgba(0,0,0,0.5) 0 0 0 9999px;
  margin: auto;
  position: absolute;
  top: 10%;
  left: 10%;
  right: 10%;
  width: 80vh;
  height: 80vh;
  border-radius: 30px;
  /*    ,       .    ~   */
  background-image: url(../../../public/homedesign/images/day_mafia.gif);
  background-repeat: no-repeat;
  background-size: cover;
}
.voteResultNight {
  z-index: 1800;
  box-shadow : rgba(0,0,0,0.5) 0 0 0 9999px;
  margin: auto;
  position: absolute;
  top: 10%;
  left: 10%;
  right: 10%;
  width: 80vh;
  height: 80vh;
  border-radius: 30px;
  /*    ,       .    ~   */
  background-image: url(../../../public/homedesign/images/wait_mafia.gif);
  background-repeat: no-repeat;
  background-size: cover;
}
.redChar {
  cursor: pointer;
  position: absolute;
  left: 32%;
  top: 52%;
  height: 35vh;
  width: 35vh;
  background-size: cover;
  background-image: url(../../../public/homedesign/images/mafia_red.png);
  transition: all ease 3s;
}
.blueChar {
  cursor: pointer;
  position: absolute;
  left: 32%;
  top: 52%;
  height: 35vh;
  width: 35vh;
  background-size: cover;
  background-image: url(../../../public/homedesign/images/mafia_blue.png);
  transition: all ease 3s;
}
.whiteChar {
  cursor: pointer;
  position: absolute;
  left: 32%;
  top: 52%;
  height: 35vh;
  width: 35vh;
  background-size: cover;
  background-image: url(../../../public/homedesign/images/mafia_white.png);
  transition: all ease 3s;
}
.blackChar {
  cursor: pointer;
  position: absolute;
  left: 32%;
  top: 52%;
  height: 35vh;
  width: 35vh;
  background-size: cover;
  background-image: url(../../../public/homedesign/images/mafia_black.png);
  transition: all ease 3s;
}
.yellowChar {
  cursor: pointer;
  position: absolute;
  left: 32%;
  top: 52%;
  height: 35vh;
  width: 35vh;
  background-size: cover;
  background-image: url(../../../public/homedesign/images/mafia_yellow.png);
  transition: all ease 3s;
}
.redCharGone {
  transform: rotate(540deg);
  left: 90%;
  top: 10%;
  height: 0px;
  width: 0px;
}
.blueCharGone {
  transform: rotate(540deg);
  left: 90%;
  top: 10%;
  height: 0px;
  width: 0px;
}
.whiteCharGone {
  transform: rotate(540deg);
  left: 90%;
  top: 10%;
  height: 0px;
  width: 0px;
}
.yellowCharGone {
  transform: rotate(540deg);
  left: 90%;
  top: 10%;
  height: 0px;
  width: 0px;
}
.blackCharGone {
  transform: rotate(540deg);
  left: 90%;
  top: 10%;
  height: 0px;
  width: 0px;
}
.deadShadow {
  position: absolute;
  top: 85%;
  left: 40%;
  width: 20vh;
  height: 20vh;
  transition: all ease 3s;
  background-image: url(../../../public/homedesign/images/mafia_shadow.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.deadShadowGone {
  top: 70%;
  left: 90%;
  width: 0px;
  height: 0px;
}
</style>