import {
  getFriends,
  followFriend,
  removeFriend,
  searchFriend,
} from "@/api/mypage.js";

const mypageStore = {
  namespaced: true,
  state: {
    friendList: "",
    friendSearchList: "",
    isThereSearch: true,
    followNum: 0,
  },
  getters: {

  },
  mutations: {
    SET_FRIENDS: (state, friends) => {
      state.friendList = friends;
      state.followNum = friends.length;
    },
    SET_FRIENDS_SEARCH: (state, friends) => {
      state.friendSearchList = friends;
    },
    SET_SEARCH_ANSWER: (state, ans) => {
      state.isThereSearch = ans;
    },
    SET_FOLLOWERS_NUM_PLUS: (state, num) => {
      state.followNum += num;
    },
    SET_FOLLOWERS_NUM_MINUS: (state, num) => {
      state.followNum -= num;
    }
  },
  actions: {
    async getFriendsStore({ commit }, userid) {
      await getFriends(
        userid,
        (response) => { //    response   
          if (response.data.length != 0) { 
            console.log(response)
            console.log(response.data)
            console.log(response.data.length)
            // response.data. , response.  
            commit("SET_FRIENDS", response.data);
          } else {
            commit("SET_FRIENDS", response.data);
            console.log(" ")
          }
        },
        (error) => {
          console.log(error);
        },
      );
    },
    // actions commit  ?  follow, remove 
    //     
    async followFriendStore({ commit }, user) {
      await followFriend(
        user,
        (response) => {
          if (response.data.message === "success") {
            console.log(" ")
            commit("SET_FOLLOWERS_NUM_PLUS", 1);
          } else {
            // console.log(" ")
          }
        },
        (error) => {
          console.log(error);
        },
      );
    },

    async removeFriendStore({ commit }, user) {
      console.log(" ?")
      console.log(user);
      await removeFriend(
        user,
        (response) => {
          console.log("  ?")
          console.log(response);
          if (response.data.message === "success") {
            console.log(" ")
            commit("SET_FOLLOWERS_NUM_MINUS", 1);
          } else {
            // console.log(" ")
          }
        },
        (error) => {
          console.log(error);
        },
      );
    },

    async searchFriendStore({ commit }, follow) {
      await searchFriend(
        follow,
        (response) => {
          if (response.data.length != 0) {
            console.log(" ?")
            commit("SET_FRIENDS_SEARCH", response.data);
            console.log(response.data);
            commit("SET_SEARCH_ANSWER", true);
          } else {
            console.log(" ");
            commit("SET_SEARCH_ANSWER", false);
            commit("SET_FRIENDS_SEARCH", response.data);
          }
        },
        (error) => {
          console.log(error);
          commit("SET_SEARCH_ANSWER", false);
        },
      );
    },

  },
};

export default mypageStore;

